@import '@carbon/themes/scss/themes';

$carbon--theme: $carbon--theme--g90;
@include carbon--theme();

@import 'carbon-components/scss/globals/scss/styles.scss';

.icon-button {
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  height: 3rem;
  width: 3rem;
  padding: 0;
  border: none;
}

.icon-button:hover {
  background-color: #353535;
}

.side-panel {
  display: flex;
  flex-direction: column;
  padding: 64px 16px;
  align-items: center;
  justify-content: space-between;
  z-index: 0;
}

.analysis-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: $ui-02;
}

.analysis-card + .analysis-card {
  margin-left: 1rem;
}

.progress-total {
  width: 80%;
  height: 2rem;
  background-color: $inverse-02;
  border-radius: 16px;
  position: relative;
  display: grid;
  place-items: center;
}

.progress-current {
  background-color: $support-02;
  height: 100%;
  position: absolute;
  border-radius: 16px;
  left: 0;
  top: 0;
}

.progress-text {
  color: $inverse-01;
  font-size: 1.2rem;
  z-index: 1;
}
